<template>
    <v-container v-bind:style="{color: colorTexto, fontSize: textSize + 'px', fontFamily: textFam, background: fondo3}">
        <v-row justify="center" >
            <v-col cols="16" sm="10" align="center">
                <v-container v-bind:style="{color: colorTexto, fontSize: textSize + 'px', fontFamily: textFam, background: fondo3}">
                    <h1 v-bind:style="{color: colorTexto, fontSize: textSize*1.5 + 'px', fontFamily: textFam, background: fondo3}" align="center"
                    justify="center"
                    > METRO MINUTO
                    </h1><br>
                </v-container>
            </v-col>
        </v-row>
        <v-row justify="center" >
            <v-col>
                <v-img
                     max-height="776"
                     max-width="487"
                    src="https://www.diariodeleon.es/media/diariodeleon/images/2019/09/30//2019093012154899441.jpg"
                 ></v-img>

            </v-col>
            <v-col>
                <br>
                <div v-bind:style="{color: colorTexto, fontSize: textSize + 'px', fontFamily: textFam, background: fondo3}">
                    Caminar se incorporaba decididamente y sin complejos al conjunto de medios de 
                    transporte. Siempre es una gran opción. Puedes tardar menos que con otro medio de transporte.
                </div>
                <br>
                <div v-bind:style="{color: colorTexto, fontSize: textSize + 'px', fontFamily: textFam, background: fondo3}">
                    Con MetroMinuto  reducir los efectos nocivos del automóvil en medio urbano: menor contaminacion, menos humos,
                    menos ruidos, menos agresividad, menos violencia vial, más seguridad, más uso diverso del espacio público, menos obstáculos,
                    mayor accesibilidad universal, mejora de la movilidad infantil, menos riesgos para todos, mayor bienestar, en difinitiva.
                </div>
            </v-col>
        </v-row>
    </v-container>
</template>
<script>
export default {
    data(){
        return{
            fondo1: "#64B5F6", 
            fondo2: "#90CAF9", 
            fondo3: "#BBDEFB",
            colorFondoTexto: "",
            tipoLetra: "",
            textSize: 20,
            textFam: "Helvetica",
            colorTexto: "white",
            fondoTexto: "transparent",
        }
    },

      /////////////////////
     //     MOUNTED     //
    /////////////////////
     mounted(){
      console.log("ESTOY ENN MOUNTED");
      console.log(this.$route.query.colorTexto);
      this.textSize = this.$route.query.textSize;
      this.textFam = this.$route.query.textFam;
      this.colorFondoTexto = this.$route.query.colorFondoTexto;
    },

      /////////////////////
     //      WATCH      //
    /////////////////////
    watch:{

      /****** CAMBIAR TIPOGRAFÍA ******/
      textFam:function() {
        if(this.textFam == "Por defecto"){
            this.textFam = "Helvetica";
        }
      },
      
      /****** CAMBIAR COLORES ******/
      colorFondoTexto:function() {
        switch(this.colorFondoTexto){
          case "Negro sobre Blanco":
            this.fondo1 = "#F5F5F5";
            this.fondo2 = "#FAFAFA"; 
            this.fondo3 = "#FFFFFF";
            this.colorTexto = "black";
            break;
          case "Blanco sobre Negro":
            this.fondo1 = "#212121"; 
            this.fondo2 = "#424242";
            this.fondo3 = "#616161";
            this.colorTexto = "white";
            break;
          case "Negro sobre Amarillo":
            this.fondo1 = "#FFEE58"; 
            this.fondo2 = "#FFF176";
            this.fondo3 = "#FFF9C4";
            this.colorTexto = "black";
            break;
          case "Amarillo sobre Negro":
            this.colorTexto = "yellow";
            this.fondo1 = "#212121"; 
            this.fondo2 = "#424242";
            this.fondo3 = "#616161";
            break; 
          case "Por defecto":
          default:
            this.fondoTexto = "transparent";
            this.colorTexto = "white";
            this.fondo1 = "#64B5F6"; 
            this.fondo2 = "#90CAF9";
            this.fondo3 = "#BBDEFB";
            break;
        }
      }
    },
  }
</script>