<template > 
  <v-container  v-bind:style="{background: fondo3}">  
     <h1  v-bind:style="{color: colorTexto, fontSize: textSize*1.5 + 'px', fontFamily: textFam, background: fondo3}">PARADAS PRÓXIMAS A SU UBICACIÓN</h1>
  </v-container>
</template>

<script>
  export default {
    data () {
      
      return {
        fondo1: "#64B5F6", 
        fondo2: "#90CAF9", 
        fondo3: "#BBDEFB",
        colorFondoTexto: "",
        tipoLetra: "",
        textSize: 20,
        path: "",
        textFam: "Helvetica",
        colorTexto: "black",
        fondoTexto: "transparent",
      }
    },

      /////////////////////
     //     MOUNTED     //
    /////////////////////
     mounted(){
      console.log("ESTOY ENN MOUNTED");
      console.log(this.$route.query.colorTexto);
      this.textSize = this.$route.query.textSize;
      this.textFam = this.$route.query.textFam;
      this.colorFondoTexto = this.$route.query.colorFondoTexto;
      this.path = this.$route.query.path;
      this.cambiarPantalla(this.path);
    },

      /////////////////////
     //      WATCH      //
    /////////////////////
    watch:{
      
      /****** CAMBIAR TIPOGRAFÍA ******/
      textFam:function() {
        if(this.textFam == "Por defecto"){
            this.textFam = "Helvetica";
        }
      },
      
      /****** CAMBIAR COLORES ******/
      colorFondoTexto:function() {
        switch(this.colorFondoTexto){
          case "Negro sobre Blanco":
            this.fondo1 = "#F5F5F5";
            this.fondo2 = "#FAFAFA"; 
            this.fondo3 = "#FFFFFF";
            this.colorTexto = "black";
            break;
          case "Blanco sobre Negro":
            this.fondo1 = "#212121"; 
            this.fondo2 = "#424242";
            this.fondo3 = "#616161";
            this.colorTexto = "white";
            break;
          case "Negro sobre Amarillo":
            this.fondo1 = "#FFEE58"; 
            this.fondo2 = "#FFF176";
            this.fondo3 = "#FFF9C4";
            this.colorTexto = "black";
            break;
          case "Amarillo sobre Negro":
            this.colorTexto = "yellow";
            this.fondo1 = "#212121"; 
            this.fondo2 = "#424242";
            this.fondo3 = "#616161";
            break; 
          case "Por defecto":
          default:
            this.fondoTexto = "transparent";
            this.colorTexto = "black";
            this.fondo1 = "#64B5F6"; 
            this.fondo2 = "#90CAF9";
            this.fondo3 = "#BBDEFB";
            break;
        }
      }
    },
       /////////////////////
     //     MÉTODOS     //
    /////////////////////
    methods: {

        /****** CAMBIAR PANTALLA ******/
        cambiarPantalla(pantalla) {
            console.log(this.$route.path);
            this.$router.replace({ path: pantalla, query:{
              colorTexto: this.colorTexto,
              colorFondoTexto: this.colorFondoTexto,
              textSize: this.textSize,
              textFam: this.textFam,
            } });
        }
    }
  }
</script>