<template > 
  <v-container  v-bind:style="{background: fondo3}">  
    <v-data-table
      :headers="headers"
      :items="desserts"
      :items-per-page="15"
      class= "colorTexto--text,"
      :item-class="{color: colorTexto, fontSize: textSize+12 + 'px', fontFamily: textFam}"
      v-bind:style="{color: colorTexto, fontSize: textSize+12 + 'px', fontFamily: textFam, background: fondo3}"
      height="600"
    > 
    </v-data-table> 
  </v-container>
</template>

<script>
  export default {
    data () {
      
      return {
        fondo1: "#64B5F6", 
        fondo2: "#90CAF9", 
        fondo3: "#BBDEFB",
        colorFondoTexto: "",
        tipoLetra: "",
        textSize: 20,
        textFam: "Helvetica",
        colorTexto: "black",
        fondoTexto: "transparent",
        headers: [
          {
            text: 'Tipo De Servicio',
            align: 'start',
            sortable: false,
            value: 'name',
          },
          { text: 'Precio/Viaje', value: 'euros' , },
           {text: 'Precio/10 Viajes', value: 'viajes'},
          
        ],
        desserts: [
          {
            name: 'Billete ordinario',
            euros: '1.20€',
            viajes: '-',
            
          },
          {
            name: 'Billete festivo',
            euros: '1.20€',
            viajes: '-',
            
          },
          {
            name: 'Servicios Especiales',
            euros: '1.30€',
            viajes: '-',
            
          },
          {
            name: 'TC Ordinaria',
            euros: '0.75€/viaje'   ,
            viajes : '7.50€/10 viajes',
            
          },
          {
            name: 'TC Especial Pensionista',
            euros: '0.30€/viaje',
            viajes : '3.00€/10 viajes',
            
          },
          {
            name: 'TC Pensionista',
            euros: '0.65€/viaje',
            viajes : '6.50€/10 viajes',
          },
          {
            name: 'TC Discapacitado',
            euros: '0.65€/viaje',
            viajes : '6.50€/10 viajes',
          },
          {
            name: 'TC Familia Numerosa',
            euros: '0.65€/viaje',
            viajes : '6.50€/10 viajes',
          },
          {
            name: 'TC Estudiante',
            euros: '0.65€/viaje',
            viajes : '6.50€/10 viajes',
          },
          {
            name: 'Abono Mensual',
            euros: '36.15€',
            viajes: '-',
          },
          {
            name: 'Abono Anual',
            euros: '308.50€',
            viajes: '-',
          },
          {
            name: 'Expedición Tarjetas Bonificadas',
            euros: '4.60€',
            viajes: '-',
          },
          {
            name: 'Renovación o Pérdida Tarjetas Bonificadas',
            euros: '9.05€',
            viajes: '-',
          },
          {
            name: 'Tarjeta Ciudadana(SIN VIAJES)',
            euros: '2.00€',
            viajes: '-',
          },
        ],
      }
    },

      /////////////////////
     //     MOUNTED     //
    /////////////////////
     mounted(){
      console.log("ESTOY ENN MOUNTED");
      console.log(this.$route.query.colorTexto);
      this.textSize = this.$route.query.textSize;
      this.textFam = this.$route.query.textFam;
      this.colorFondoTexto = this.$route.query.colorFondoTexto;
    },

      /////////////////////
     //      WATCH      //
    /////////////////////
    watch:{

      /****** CAMBIAR TIPOGRAFÍA ******/
      textFam:function() {
        if(this.textFam == "Por defecto"){
            this.textFam = "Helvetica";
        }
      },
      
      /****** CAMBIAR COLORES ******/
      colorFondoTexto:function() {
        switch(this.colorFondoTexto){
          case "Negro sobre Blanco":
            this.fondo1 = "#F5F5F5";
            this.fondo2 = "#FAFAFA"; 
            this.fondo3 = "#FFFFFF";
            this.colorTexto = "black";
            break;
          case "Blanco sobre Negro":
            this.fondo1 = "#212121"; 
            this.fondo2 = "#424242";
            this.fondo3 = "#616161";
            this.colorTexto = "white";
            break;
          case "Negro sobre Amarillo":
            this.fondo1 = "#FFEE58"; 
            this.fondo2 = "#FFF176";
            this.fondo3 = "#FFF9C4";
            this.colorTexto = "black";
            break;
          case "Amarillo sobre Negro":
            this.colorTexto = "yellow";
            this.fondo1 = "#212121"; 
            this.fondo2 = "#424242";
            this.fondo3 = "#616161";
            break; 
          case "Por defecto":
          default:
            this.fondoTexto = "transparent";
            this.colorTexto = "black";
            this.fondo1 = "#64B5F6"; 
            this.fondo2 = "#90CAF9";
            this.fondo3 = "#BBDEFB";
            break;
        }
      }
    },
  }
</script>