<template>
  <v-app id="barra">
    <v-card height="60px">
      <!-- BARRA SUPERIOR -->
      <v-toolbar
        v-bind:style="{background: fondo1}"
      >
        <!-- Título -->            
        <v-toolbar-title> 
          <div v-bind:style="{color: colorTexto, fontSize: textSize+12 + 'px', fontFamily: textFam}">
            TRANSPORTE PÚBLICO LEÓN</div>
        </v-toolbar-title>
        <!-- Separador -->
        <v-spacer></v-spacer>
        <!-- Botón Configuración -->
        <v-btn @click="drawer = !drawer"  outlined depressed rounded class="header-toolbar-learner-options-toggle btn-link js-header-learner-options-dropdown-toggle" id="header-toolbar-learner-options-toggle" aria-haspopup="true" aria-expanded="false" v-bind:style="{color: colorTexto}">
            <v-icon > mdi-cog </v-icon>
            <div v-bind:style="{color: colorTexto, fontSize: textSize-8 + 'px', fontFamily: textFam}"> Configuración </div>
        </v-btn>
        <!-- Botón Ayuda -->
        
        <div class="text-center">
      
      <template>
        <v-btn
          outlined depressed rounded class="header-toolbar-learner-options-toggle btn-link js-header-learner-options-dropdown-toggle" id="header-toolbar-learner-options-toggle" aria-haspopup="true" aria-expanded="false" v-bind:style="{color: colorTexto}"
          @click.stop="dialog = true"
        >
          <div v-bind:style="{color: colorTexto, fontSize: textSize-8 + 'px', fontFamily: textFam}"> Ayuda </div>
        </v-btn>
      
    <v-dialog
      v-model="dialog"
      width="500"
    >
      <v-card>
        <v-card-title v-bind:style="{background: fondo1}">
          <div v-bind:style="{color: colorTexto, fontSize: textSize-8 + 'px', fontFamily: textFam}"> AYUDA </div>
        </v-card-title>

        <v-divider></v-divider>  

        <v-expansion-panels>

        <v-expansion-panel>
          <v-expansion-panel-header>
            <div v-bind:style="{color: black, fontSize: textSize-8 + 'px', fontFamily: textFam}"> 
              Consultar las líneas </div>
          </v-expansion-panel-header>
          <v-expansion-panel-content>
            <div v-bind:style="{color: black, fontSize: textSize-8 + 'px', fontFamily: textFam}">
               En la pestaña de líneas podrás consultar los horarios de las paradas pricipales de cada línea. 
               Además, puedes ver todas las paradas de cada línea, así podrás calcular la hora de llegada a la parada que te interese. </div>
          </v-expansion-panel-content>
        </v-expansion-panel>

        <v-expansion-panel>
        <v-expansion-panel-header>
            <div v-bind:style="{color: black, fontSize: textSize-8 + 'px', fontFamily: textFam}"> 
              Mapa de paradas próximas</div>
          </v-expansion-panel-header>
          <v-expansion-panel-content>
            <div v-bind:style="{color: black, fontSize: textSize-8 + 'px', fontFamily: textFam}">
              En la pestaña de Paradas próximas podrás consultar en un mapa las paradas más cercanas.
              También puedes guardar una ubicación determinada, para poder ver más adelante las paradas próximas a esta.</div>
          </v-expansion-panel-content>
        </v-expansion-panel>

        <v-expansion-panel>
        <v-expansion-panel-header>
            <div v-bind:style="{color: black, fontSize: textSize-8 + 'px', fontFamily: textFam}"> 
              MetroMinuto </div>
          </v-expansion-panel-header>
          <v-expansion-panel-content>
            <div v-bind:style="{color: black, fontSize: textSize-8 + 'px', fontFamily: textFam}">
              El mapa MetroMinuto muestra los tiempos a pie entre los enclaves más importantes de la ciudad. 
              Andar también es una gran forma de recorrer la ciudad, así reduces la contaminación y ayudas al planeta.</div>
          </v-expansion-panel-content>
        </v-expansion-panel>

        <v-expansion-panel>
        <v-expansion-panel-header>
            <div v-bind:style="{color: black, fontSize: textSize-8 + 'px', fontFamily: textFam}"> 
              Botón de Configuración </div>
          </v-expansion-panel-header>
          <v-expansion-panel-content>
            <div v-bind:style="{color: black, fontSize: textSize-8 + 'px', fontFamily: textFam}">
              El botón de configuración nos ofrece tres botones de interacción que nos permitirán
              cambiar el tamañ del texto, el tipo de letra y los colores de letras y fondo. De esta manera
              podemos adaptar el contenido para hacerlo más legible o adecuarlo a nuestro gusto.</div>
          </v-expansion-panel-content>
        </v-expansion-panel>

      </v-expansion-panels>
      </v-card>
    </v-dialog>
      </template>
    
  </div> 
      </v-toolbar>
    </v-card>

    <!-- BARRA CONFIGURACIÓN -->
        <v-card 
            temporary
            absolute
            v-if="drawer == true"
            v-bind:style="{background: fondo3, color: colorTexto}"
        >
            <v-container fluid grid-list-xl v-bind:style="{background: fondo3, color: colorTexto}">
                <v-layout wrap align-center >
                  <!-- Tamaño de Texto -->
                  <v-flex xs12 sm4 d-flex>
                      <div v-bind:style="{color: colorTexto, fontSize: textSize + 'px', fontFamily: textFam}">Tamaño Texto</div>
                  </v-flex>
                  <!-- Tipo de Letra -->
                  <v-flex xs12 sm4 d-flex> 
                      <div v-bind:style="{color: colorTexto, fontSize: textSize + 'px', fontFamily: textFam}">Tipo de Letra</div>
                  </v-flex>
                  <!-- Colores Fondo -->
                  <v-flex xs12 sm4 d-flex> 
                      <div v-bind:style="{color: colorTexto, fontSize: textSize + 'px', fontFamily: textFam}">Colores</div>
                  </v-flex>

                  <!-- Tamaño de Texto -->
                    <v-flex xs12 sm4 d-flex>
                        <v-slider
                            v-model="textSize"
                            :thumb-size="textSize"
                            thumb-label="always" class="align-center"
                            :max="max"
                            :min="min"
                            hide-details
                            v-bind:style="{background: fondo3, color: colorTexto}"
                        >
                            <template v-slot:append >
                                <v-text-field
                                    v-model="textSize"
                                    class="mt-0 pt-0"
                                    hide-details
                                    single-line
                                    type="number"
                                    v-bind:style="{background: fondo3, color: colorTexto, fontSize: textSize + 'px', fontFamily: textFam}"
                                ></v-text-field>
                            </template>
                        </v-slider> 
                    </v-flex>
                    <!-- Tipo de Letra -->
                    <v-flex xs12 sm4 d-flex v-bind:style="{background: fondo3, color: colorTexto}"> 
                        <v-select 
                        :items="tiposLetra"
                        v-model="textFam"
                        label="Tipo de Letra"
                        v-bind:style="{background: fondo3, color: colorTexto, fontSize: textSize + 'px', fontFamily: textFam}"
                        outline
                        >
                        </v-select>
                    </v-flex>
                    <!-- Colores Fondo -->
                    <v-flex xs12 sm4 d-flex> 
                        <v-select
                        :items="coloresFondoTexto"
                        label="Colores"
                        outline
                        v-model="colorFondoTexto"
                        v-bind:style="{background: fondo3, color: colorTexto}"
                        >
                        </v-select>
                    </v-flex>
                </v-layout>
            </v-container>
        </v-card>

    <!-- BARRA MENÚ IF -->
    <v-card v-if="textSize <= 23"
        height="70px"  
        align="center"
        justify="center"
    >
        <v-toolbar
            v-bind:style="{background: fondo2, color: colorTexto}"
            height="70px" 
            align="center"
            justify="center"
            depressed
            rounded
        >
            <!-- Separador -->
            <v-spacer></v-spacer>
            <div class="d-flex flex-no-wrap justify-space-between">
                <div v-bind:style="{background: fondo2, color: colorTexto}">
                <!-- Botones -->
                <v-btn v-for="item in items"
                    :key="item.title"
                    @click="cambiarPantalla(item.link)"
                    outlined
                    depressed
                    rounded
                    v-bind:style="{background: fondo2, color: colorTexto}"
                    class="d-flex-no-wrap justify-space-between"
                    align="center"
                    justify="center"
                >
                    <v-icon> {{ item.icon }} </v-icon>
                    <div v-bind:style="{color: colorTexto, fontSize: textSize-8 + 'px', fontFamily: textFam}"> {{ item.title }} </div>
                </v-btn>
                </div>
            </div>
            <!-- Separador -->
        <v-spacer></v-spacer>
      </v-toolbar>
    </v-card>
    <!-- BARRA MENÚ ELSE -->
    <v-card v-else
        height="95px"  
        align="center"
        justify="center"
    >
        <v-toolbar
            v-bind:style="{background: fondo2, color: colorTexto}"
            height="95px" 
            align="center"
            justify="center"
            depressed
            rounded
        >
            <!-- Separador -->
            <v-spacer></v-spacer>
            <div class="d-flex flex-no-wrap justify-space-between">
                <div v-bind:style="{background: fondo2, color: colorTexto}">
                <!-- Botones -->
                <v-btn v-for="item in items"
                    :key="item.title"
                    @click="cambiarPantalla(item.link)"
                    outlined
                    depressed
                    rounded
                    class="d-flex-no-wrap justify-space-between"
                    align="center"
                    justify="center"
                    v-bind:style="{background: fondo2, color: colorTexto}"
                >
                    <v-icon v-bind:style="{color: colorTexto}"> {{ item.icon }} </v-icon>
                    <div v-bind:style="{color: colorTexto, fontSize: textSize + 'px', fontFamily: textFam}"> {{ item.title }} </div>
                </v-btn>
                </div>
            </div>
            <!-- Separador -->
        <v-spacer></v-spacer>
        </v-toolbar>
    </v-card>
  
    <!-- CONTENIDO -->
    <v-main> 
    </v-main>
    <router-view />
  </v-app>
</template>

<script>
  document.cookie = 'same-site-cookie=foo; SameSite=Lax'; 
  document.cookie = 'cross-site-cookie=bar; SameSite=None; Secure';
  export default {
    props: {
      source: String
    },
    name: "App",
    data() {
      return {
        dialog: false,
        name: "inicio",
        busqueda: "",
        fondo1: "#64B5F6", 
        fondo2: "#90CAF9", 
        fondo3: "#BBDEFB",
        colorFondoTexto: "",
        tipoLetra: "",
        textSize: 20,
        oldTextSize: 20,
        textFam: "Helvetica",
        colorTexto: "white",
        fondoTexto: "transparent",
        drawer: false,
        min: 20,
        max: 40,
        slider: 0,
        zoom:0,
        slides: [
          {
            position: "center",
            text: "Muévete cómodamente por la ciudad.",
            src: "https://www.diariodeleon.es/media/diariodeleon/images/2014/06/14/897362_1.jpg",
            class: "display-3"
          },
          {
            position: "center",
            text: "Descubre las maravillas que esconde la ciudad.",
            src: "https://viajes.nationalgeographic.com.es/medio/2018/09/04/la-famosa-catedral-de-leon_7d8126d8_1500x1047.jpg",
            class: "display-3 white--text"
          },
          {
            position: "center",
            text: "Llegar al centro de la ciudad es mucho más rápido en cercanías.",
            src: "https://upload.wikimedia.org/wikipedia/commons/6/65/FEVE_station_Leon.JPG",
            class: "display-3"
          },
          {
            position: "center",
            text: "Desplázate cómodamente gracias al carril bici y al servicio de préstamo de bicicletas.",
            src: "https://upload.wikimedia.org/wikipedia/commons/thumb/7/79/Servicio_de_pr%C3%A9stamo_de_bicicletas%2C_Le%C3%B3n.jpg/1600px-Servicio_de_pr%C3%A9stamo_de_bicicletas%2C_Le%C3%B3n.jpg",
            class: "display-3 white--text"
          }
        ],
        items: [
          { title: "Inicio", icon: "mdi-home", link: "/" },
          { title: "Tarifas", icon: "mdi-cash-100", link: "/tarifas" },
          { title: "Líneas", icon: "mdi-swap-horizontal-circle", link: "/lineas" },   //swap-vertical
          { title: "Paradas Próximas", icon: "mdi-map-marker", link: "/paradas" },
          { title: "MetroMinuto", icon: "mdi-transit-transfer", link: "/metrominuto" }, //timer-outline //clock-outline //clock-fast
          { title: "Mapa de líneas", icon: "mdi-train", link: "/autobuses" },
          { title: "Lugares de Interés", icon: "mdi-camera", link: "/sitiosRecomendados" },
        ],
        coloresFondoTexto: [ "Por defecto", "Negro sobre Blanco", "Blanco sobre Negro", "Negro sobre Amarillo", "Amarillo sobre Negro" ],
        tiposLetra: [ "Helvetica", "Times New Roman", "Arial", "Verdana", "Roboto" ],
        coloresFondoTextoD: [
            { name: "Negro sobre Blanco", class: "fl-preview-theme fl-theme-uio-bw fl-theme-bw", value: "bw"},
            { name: "Blanco sobre Negro", class: "fl-preview-theme fl-theme-uio-wb fl-theme-wb", value: "wb"},
            { name: "Negro sobre Amarillo", class: "fl-preview-theme fl-theme-uio-by fl-theme-by", value: "by"},
            { name: "Amarillo sobre Negro", class: "fl-preview-theme fl-theme-uio-yb fl-theme-yb", value: "yb"},
        ],
        tiposLetraD: [
            { name: "Helvetica", class: "fl-preview-theme fl-font-uio-times", value: "times"},
            { name: "Times New Roman", class: "fl-preview-theme fl-font-uio-times", value: "times"},
            { name: "Arial", class: "fl-preview-theme fl-font-uio-arial", value: "arial"},
            { name: "Verdana", class: "fl-preview-theme fl-font-uio-verdana", value: "verdana"},
            { name: "Roboto", class: "fl-preview-theme fl-font-uio-comic-sans", value: "roboto"},
        ],
        colores: [
            { fondo1: "#64B5F6", fondo2: "#90CAF9" },
        ],
        colors: ["#952175", "#00ACC1", "#FFB300", "#E91E63", "#8BC34A"],
      };  
    },

      /////////////////////
     //      WATCH      //
    /////////////////////
    watch:{

      /****** CAMBIAR TAMAÑO TEXTO ******/
      textSize:function (){
        if(this.oldTextSize != this.textSize){
          this.oldTextSize = this.textSize;
          this.cambiarPantalla(this.$route.path);
        }
      },
      /****** CAMBIAR TIPOGRAFÍA ******/
      textFam:function() {
        if(this.textFam == "Por defecto"){
            this.textFam = "Helvetica";
        }
        this.cambiarPantalla(this.$route.path);
      },
      /****** CAMBIAR COLORES ******/
      colorFondoTexto:function() {
        switch(this.colorFondoTexto){
          case "Negro sobre Blanco":
            this.fondo1 = "#F5F5F5";
            this.fondo2 = "#FAFAFA"; 
            this.fondo3 = "#FFFFFF";
            this.colorTexto = "black";
            break;
          case "Blanco sobre Negro":
            this.fondo1 = "#212121"; 
            this.fondo2 = "#424242";
            this.fondo3 = "#616161";
            this.colorTexto = "white";
            break;
          case "Negro sobre Amarillo":
            this.fondo1 = "#FFEE58"; 
            this.fondo2 = "#FFF176";
            this.fondo3 = "#FFF9C4";
            this.colorTexto = "black";
            break;
          case "Amarillo sobre Negro":
            this.colorTexto = "yellow";
            this.fondo1 = "#212121"; 
            this.fondo2 = "#424242";
            this.fondo3 = "#616161";
            break; 
          case "Por defecto":
          default:
            this.fondoTexto = "transparent";
            this.colorTexto = "white";
            this.fondo1 = "#64B5F6"; 
            this.fondo2 = "#90CAF9";
            this.fondo3 = "#BBDEFB";
            break;
        }
        this.cambiarPantalla(this.$route.path);
      },
    },

      /////////////////////
     //     MÉTODOS     //
    /////////////////////
    methods: {

        /****** CAMBIAR PANTALLA ******/
        cambiarPantalla(pantalla) {
           if(this.textFam == "Por defecto"){
              this.textFam = "Helvetica";
          }
            this.$router.replace({ path: "/aux", query:{
              colorTexto: this.colorTexto,
              colorFondoTexto: this.colorFondoTexto,
              textSize: this.textSize,
              textFam: this.textFam,
              path: pantalla,
            } });
        },

        /****** BOTÓN AYUDA ******/
        ayuda() {
        },

        /****** ZOOM ******/
        zoomOut () {
            this.zoom = (this.zoom - 10) || 0
        },
        zoomIn () {
            this.zoom = (this.zoom + 10) || 100
        },
    }
};
</script>

