<template>
  <v-app id="autobuses">
    <!-- CONTENIDO -->
    <v-main> 
      <!-- CARRUSEL DE PREVISUALIZACIONES -->
      <v-carousel
      cycle
      height="600"
      hide-delimiter-background
      show-arrows-on-hover
      align="center"
      justify="center"
      >
        <v-carousel-item
          v-for="(slide, i) in this.slides"
          :key="i"
          :src="slide.src"
          :position="slide.position"
        >
          <v-row class="fill-height" align="center" justify="center">   
            <div v-bind:style="{color: colorTexto, fontSize: textSize*2 + 'px', fontFamily: textFam, background: fondoTexto}">{{ slide.text }}</div>     
          </v-row>
        </v-carousel-item>
      </v-carousel>
    </v-main>
  </v-app>
</template>

<script>
  export default {
    props: {
      source: String
    },
    name: "App",
    data() {
      return {
        fondo1: "#64B5F6", 
        fondo2: "#90CAF9", 
        fondo3: "#BBDEFB",
        colorFondoTexto: "",
        tipoLetra: "",
        textSize: 20,
        textFam: "Helvetica",
        colorTexto: "white",
        fondoTexto: "transparent",
        slides: [
          {
            position: "center",
            text: "Muévete cómodamente por la ciudad.",
            src: "https://www.diariodeleon.es/media/diariodeleon/images/2014/06/14/897362_1.jpg",
            class: "display-3 "
          },
          {
            position: "center",
            text: "Descubre las maravillas que esconde la ciudad.",
            src: "https://viajes.nationalgeographic.com.es/medio/2018/09/04/la-famosa-catedral-de-leon_7d8126d8_1500x1047.jpg",
            class: "display-3 white--text"
          },
          {
            position: "center",
            text: "Llegar al centro de la ciudad es mucho más rápido en cercanías.",
            src: "https://upload.wikimedia.org/wikipedia/commons/6/65/FEVE_station_Leon.JPG",
            class: "display-3"
          },
          {
            position: "center",
            text: "Desplázate cómodamente gracias al carril bici y al servicio de préstamo de bicicletas.",
            src: "https://upload.wikimedia.org/wikipedia/commons/thumb/7/79/Servicio_de_pr%C3%A9stamo_de_bicicletas%2C_Le%C3%B3n.jpg/1600px-Servicio_de_pr%C3%A9stamo_de_bicicletas%2C_Le%C3%B3n.jpg",
            class: "display-3 white--text"
          }
        ]
      };  
    },

      /////////////////////
     //     MOUNTED     //
    /////////////////////
     mounted(){
      console.log("ESTOY ENN MOUNTED");
      console.log(this.$route.query.colorTexto);
      this.textSize = this.$route.query.textSize;
      this.textFam = this.$route.query.textFam;
      this.colorFondoTexto = this.$route.query.colorFondoTexto;
    },

      /////////////////////
     //      WATCH      //
    /////////////////////
    watch:{

      /****** CAMBIAR TIPOGRAFÍA ******/
      textFam:function() {
        if(this.textFam == "Por defecto"){
            this.textFam = "Helvetica";
        }
      },
      
      /****** CAMBIAR COLORES ******/
      colorFondoTexto:function() {
        switch(this.colorFondoTexto){
          case "Negro sobre Blanco":
            this.fondo1 = "#F5F5F5";
            this.fondo2 = "#FAFAFA"; 
            this.fondo3 = "#FFFFFF";
            this.colorTexto = "black";
            break;
          case "Blanco sobre Negro":
            this.fondo1 = "#212121"; 
            this.fondo2 = "#424242";
            this.fondo3 = "#616161";
            this.colorTexto = "white";
            break;
          case "Negro sobre Amarillo":
            this.fondo1 = "#FFEE58"; 
            this.fondo2 = "#FFF176";
            this.fondo3 = "#FFF9C4";
            this.colorTexto = "black";
            break;
          case "Amarillo sobre Negro":
            this.colorTexto = "yellow";
            this.fondo1 = "#212121"; 
            this.fondo2 = "#424242";
            this.fondo3 = "#616161";
            break; 
          case "Por defecto":
          default:
            this.fondoTexto = "transparent";
            this.colorTexto = "white";
            this.fondo1 = "#64B5F6"; 
            this.fondo2 = "#90CAF9";
            this.fondo3 = "#BBDEFB";
            break;
        }
      }
    },
};
</script>
