import Vue from 'vue'
import VueRouter from 'vue-router'
import Inicio from '../views/Inicio.vue'
import Tarifas from '../views/Tarifas.vue'
import Lineas from '../views/Lineas.vue'
import Paradas from '../views/Paradas.vue'
import Metrominuto from '../views/Metrominuto.vue'
import Autobuses from '../views/Autobuses.vue'
import PantallaAuxiliar from '../views/PantallaAuxiliar.vue'
import SitiosRecomendados from '../views/SitiosRecomendados.vue'
import Barra from '../views/Barra.vue'
import Linea1 from '../views/lineas/Linea1.vue'
import Linea2 from '../views/lineas/Linea2.vue'
import Linea3 from '../views/lineas/Linea3.vue'
import Linea4 from '../views/lineas/Linea4.vue'
import Linea5 from '../views/lineas/Linea5.vue'
import Linea6 from '../views/lineas/Linea6.vue'
import Linea7 from '../views/lineas/Linea7.vue'
import Linea8 from '../views/lineas/Linea8.vue'
import Linea9 from '../views/lineas/Linea9.vue'
import Linea10 from '../views/lineas/Linea10.vue'
import Linea11 from '../views/lineas/Linea11.vue'
import Linea12 from '../views/lineas/Linea12.vue'
import Linea13 from '../views/lineas/Linea13.vue'


Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    redirect: 'inicio'
  },

  {
    path: "/barra",
    name: "barra",
    component: Barra,

    children: [{
      path: "/lineas",
      component: Lineas
    }, 
    {
      path: "/paradas",
      component: Paradas
    },
    {
      path: "/metrominuto",
      component: Metrominuto
    },
    {
      path: "/autobuses",
      component: Autobuses
    },
    {
      path: "/aux",
      component: PantallaAuxiliar
    },
    {
      path: "/tarifas",
      component: Tarifas
    },
    {
      path: "/inicio",
      component: Inicio
    },
    {
      path: "/sitiosRecomendados",
      component: SitiosRecomendados
    },
    {
      path: "/linea1",
      component: Linea1
    },
    {
      path: "/linea2",
      component: Linea2
    },
    {
      path: "/linea3",
      component: Linea3
    },
    {
      path: "/linea4",
      component: Linea4
    },
    {
      path: "/linea5",
      component: Linea5
    },
    {
      path: "/linea6",
      component: Linea6
    },
    {
      path: "/linea7",
      component: Linea7
    },
    {
      path: "/linea8",
      component: Linea8
    },
    {
      path: "/linea9",
      component: Linea9
    },
    {
      path: "/linea10",
      component: Linea10
    },
    {
      path: "/linea11",
      component: Linea11
    },
    {
      path: "/linea12",
      component: Linea12
    },
    {
      path: "/linea13",
      component: Linea13
    }
  
  ]
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
