<template>
  <v-container  v-bind:style="{background: fondo3}">
        <h1  v-bind:style="{color: colorTexto, fontSize: textSize*1.5 + 'px', fontFamily: textFam, background: fondo3}">LUGARES DE INTERÉS</h1>
        <br>
        <v-container fluid>
        <v-row dense>
            <v-col
            v-for="card in cards"
            :key="card.title"
            v-bind:style="{color: colorTexto, fontSize: textSize + 'px', fontFamily: textFam}"
            :cols="card.flex"
            >
            <v-card>
                <v-img
                    :src="card.src"
                    class="white--text align-end"
                    gradient="to bottom, rgba(0,0,0,.1), rgba(0,0,0,.5)"
                    height="300px"
                >
                </v-img>
                <div class="d-flex flex-no-wrap justify-space-between">
                    <div>
                        <v-card-title v-text="card.title"></v-card-title>
                    </div>
                    <div>
                        <v-card-actions>     
                            <v-btn
                                icon
                                @click="card.show= !card.show"
                            >
                                <v-icon>{{ card.show ? 'mdi-chevron-up' : 'mdi-chevron-down' }}</v-icon>
                            </v-btn>
                        </v-card-actions>
                    </div>
                </div>
                    <v-expand-transition>
                        <div v-show="card.show">
                            <v-divider></v-divider>
                            <v-card-text v-text="card.descr"></v-card-text>
                        </div>
                    </v-expand-transition>

            </v-card>
            </v-col>
        </v-row>
        </v-container>
  </v-container>
</template>

<script>
  export default {
    data: () => ({
        drawer: false,
        group: null,
        fondo1: "#64B5F6", 
        fondo2: "#90CAF9", 
        fondo3: "#BBDEFB",
        colorFondoTexto: "",
        tipoLetra: "",
        descr: "",
        informacion: false,
        textSize: 20,
        textFam: "Helvetica",
        colorTexto: "white",
        fondoTexto: "transparent",
        cards: [
            { title: 'CATEDRAL', src: 'http://upload.wikimedia.org/wikipedia/commons/7/76/Catedral_de_leon.jpg', flex: 6, show: false, descr: 'La catedral de Santa María de Regla de León es un templo de culto católico, sede episcopal de la diócesis de León, España, consagrada bajo la advocación de la Virgen María. Fue el primer monumento declarado en España mediante Real Orden de 28 de agosto del año 1844 (confirmada por Real Orden el 24 de septiembre del año 1845).'},
            { title: 'CASA BOTINES', src: 'https://c2.staticflickr.com/8/7620/17020863832_ca8a36118e_b.jpg', flex: 6, show: false, descr: 'La casa Botines o casa Fernández y Andrés es un edificio de estilo modernista situado en la ciudad española de León, capital de la provincia homónima. Originalmente era un almacén comercial y residencia particular. Construido y diseñado por el arquitecto Antoni Gaudí entre 1891 y 1892, es una de sus tres obras fuera de Cataluña, junto al Palacio Episcopal de Astorga —también en la provincia de León— y El Capricho de Comillas, en Cantabria.' },
            { title: 'SAN MARCOS', src: 'https://media-cdn.tripadvisor.com/media/photo-s/02/83/a3/6d/exterior-parador.jpg', flex: 6, show: false, descr: 'El convento de San Marcos es una de las grandes joyas de la arquitectura de la ciudad española de León junto con la catedral, la basílica de San Isidoro y la Casa Botines. Se encuentra hoy convertido en parador de turismo de España, además de iglesia consagrada y, antiguamente, Museo de León, siendo uno de los monumentos más importantes del Renacimiento español.' },
            { title: 'BASÍLICA DE SAN ISIDORO', src: 'https://c2.staticflickr.com/8/7233/7220650042_142556a823_b.jpg', flex: 6, show: false, descr: 'La Real Colegiata Basílica de San Isidoro o, simplemente, San Isidoro de León, es un templo cristiano ubicado en la ciudad de León, en España. Es uno de los conjuntos arquitectónicos de estilo románico más destacados de España, por su historia, arquitectura, escultura, y por los objetos suntuarios románicos que se han podido conservar. Presenta la particularidad de tener un Panteón Real ubicado a los pies de la iglesia, con pintura mural románica y capiteles originales, todo lo cual hace que sea pieza única del mundo románico de la época. El conjunto fue construido y engrandecido durante los siglos xi y xii' },
            { title: 'PALACIO DE LOS GUZMANES', src: 'https://upload.wikimedia.org/wikipedia/commons/3/34/Fachada_del_Palacio_de_los_Guzmanes.jpg', flex: 6, show: false, descr: 'El palacio de los Guzmanes es un palacio renacentista del siglo xvi situado en la plaza de San Marcelo junto a la Casa Botines en la ciudad de León, España. Su traza se debe al maestro Rodrigo Gil de Hontañón,1​ si bien se ocupó de su ejecución Juan de Ribero Rada.2​:321 A pesar de quedar inconcluso se convirtió en el palacio más destacado de la ciudad. Ya en el siglo xx, la Diputación Provincial de León se hizo cargo de terminarlo para albergar sus nuevas oficinas, función que desempeña hasta la actualidad.'}, 
            { title: 'UNIVERSIDAD DE LEÓN', src: 'https://www.fmoinfluencer.es/wp-content/uploads/2019/05/Foto-Campus-ULE-Le%C3%B3n.jpg', flex: 6, show: false, descr: 'Universidad pública con sede en León, (España), y con un campus adicional en Ponferrada. El germen de la universidad lo encontramos en 1843, cuando se crea la Escuela Normal de Maestros o Seminario de Maestros de Instrucción Pública y en la escuela subalterna de Veterinaria, fundada en 1852, sentando las bases de la futura universidad leonesa. Fue fundada en 1979 como escisión de la Universidad de Oviedo, a partir de las diversas Escuelas y Facultades que, dependientes de aquella.'},
            { title: 'MUSAC', src: 'https://aegeegames.files.wordpress.com/2013/04/musac.jpg', flex: 12, show: false, descr: 'El Museo de Arte Contemporáneo de Castilla y León, también conocido por sus siglas MUSAC, está situado en la ciudad de León (España). Creado con el propósito de ser un "Museo del Presente" o "Museo del siglo XXI", su colección y sus exposiciones y actividades están fundamentalmente dirigidas a abarcar el arte actual en sus diferentes manifestaciones.'}, 
        ],
    
    }),
    
      /////////////////////
     //     MOUNTED     //
    /////////////////////
     mounted(){
      console.log("ESTOY ENN MOUNTED");
      console.log(this.$route.query.colorTexto);
      this.textSize = this.$route.query.textSize;
      this.textFam = this.$route.query.textFam;
      this.colorFondoTexto = this.$route.query.colorFondoTexto;
    },

      /////////////////////
     //      WATCH      //
    /////////////////////
    watch:{
      
      /****** CAMBIAR TIPOGRAFÍA ******/
      textFam:function() {
        if(this.textFam == "Por defecto"){
            this.textFam = "Helvetica";
        }
      },

      /****** CAMBIAR COLORES ******/
      colorFondoTexto:function() {
        switch(this.colorFondoTexto){
          case "Negro sobre Blanco":
            this.fondo1 = "#F5F5F5";
            this.fondo2 = "#FAFAFA"; 
            this.fondo3 = "#FFFFFF";
            this.colorTexto = "black";
            break;
          case "Blanco sobre Negro":
            this.fondo1 = "#212121"; 
            this.fondo2 = "#424242";
            this.fondo3 = "#616161";
            this.colorTexto = "white";
            break;
          case "Negro sobre Amarillo":
            this.fondo1 = "#FFEE58"; 
            this.fondo2 = "#FFF176";
            this.fondo3 = "#FFF9C4";
            this.colorTexto = "black";
            break;
          case "Amarillo sobre Negro":
            this.colorTexto = "yellow";
            this.fondo1 = "#212121"; 
            this.fondo2 = "#424242";
            this.fondo3 = "#616161";
            break; 
          case "Por defecto":
          default:
            this.fondoTexto = "transparent";
            this.colorTexto = "black";
            this.fondo1 = "#64B5F6"; 
            this.fondo2 = "#90CAF9";
            this.fondo3 = "#BBDEFB";
            break;
        }
      }
    },

      /////////////////////
     //     MÉTODOS     //
    /////////////////////
    methods:{
        informacion(descr){
            setTimeout(() =>{
                descr = !descr;
            }, 1000);
        }
    }
  }
    
</script>