<template >
  <v-container  v-bind:style="{background: fondo3}">
    <v-row justify="center" >
      <v-col cols="16" sm="10" align="center">
        <v-container v-bind:style="{color: colorTexto, fontSize: textSize + 'px', fontFamily: textFam, background: fondo3}">
          <h1 v-bind:style="{color: colorTexto, fontSize: textSize*1.5 + 'px', fontFamily: textFam, background: fondo3}" align="center"
          justify="center"
          > MAPA DE LÍNEAS
          </h1><br>
          
          <v-img
            max-height="1500"
            max-width="1000"
            src="http://www.alesa-alsa.com/2017/IMAGES/PLANO%20LEON_modif_09_2020.png"
          ></v-img>
          
        </v-container>
      </v-col>
      <v-img
            max-height="1500"
            max-width="1000"
            src="http://www.gifex.com/images/0X0/2011-02-10-12944/Lneas-de-autobuses-de-Crdoba.jpg"></v-img>
        <br>
    </v-row>
  </v-container>
</template>
<script>
export default {
    data(){
        return{
            fondo1: "#64B5F6", 
            fondo2: "#90CAF9", 
            fondo3: "#BBDEFB",
            colorFondoTexto: "",
            tipoLetra: "",
            textSize: 20,
            textFam: "Helvetica",
            colorTexto: "white",
            fondoTexto: "transparent",
        }
    },
    
      /////////////////////
     //     MOUNTED     //
    /////////////////////
     mounted(){
      console.log("ESTOY ENN MOUNTED");
      console.log(this.$route.query.colorTexto);
      this.textSize = this.$route.query.textSize;
      this.textFam = this.$route.query.textFam;
      this.colorFondoTexto = this.$route.query.colorFondoTexto;
    },

      /////////////////////
     //      WATCH      //
    /////////////////////
    watch:{

      /****** CAMBIAR TIPOGRAFÍA ******/
      textFam:function() {
        if(this.textFam == "Por defecto"){
            this.textFam = "Helvetica";
        }
      },
      
      /****** CAMBIAR COLORES ******/
      colorFondoTexto:function() {
        switch(this.colorFondoTexto){
          case "Negro sobre Blanco":
            this.fondo1 = "#F5F5F5";
            this.fondo2 = "#FAFAFA"; 
            this.fondo3 = "#FFFFFF";
            this.colorTexto = "black";
            break;
          case "Blanco sobre Negro":
            this.fondo1 = "#212121"; 
            this.fondo2 = "#424242";
            this.fondo3 = "#616161";
            this.colorTexto = "white";
            break;
          case "Negro sobre Amarillo":
            this.fondo1 = "#FFEE58"; 
            this.fondo2 = "#FFF176";
            this.fondo3 = "#FFF9C4";
            this.colorTexto = "black";
            break;
          case "Amarillo sobre Negro":
            this.colorTexto = "yellow";
            this.fondo1 = "#212121"; 
            this.fondo2 = "#424242";
            this.fondo3 = "#616161";
            break; 
          case "Por defecto":
          default:
            this.fondoTexto = "transparent";
            this.colorTexto = "white";
            this.fondo1 = "#64B5F6"; 
            this.fondo2 = "#90CAF9";
            this.fondo3 = "#BBDEFB";
            break;
        }
      }
    },
  }
</script>
