<template >
  <v-container  v-bind:style="{background: fondo3}">
    <v-row justify="center" >
      <v-col cols="5" sm="10" align="center">
        <v-container v-bind:style="{color: colorTexto, fontSize: textSize + 'px', fontFamily: textFam, background: fondo3}">
          <h1 v-bind:style="{color: colorTexto, fontSize: textSize*1.5 + 'px', fontFamily: textFam, background: fondo3}" align="center"
          justify="center"
          > PARADAS PRÓXIMAS A SU UBICACIÓN
          </h1><br>
          <div id="map" v-bind:style="{color: colorTexto, fontSize: textSize + 'px', fontFamily: textFam, background: fondo2}">  
            <Mapbox
              access-token="pk.eyJ1IjoiYXBlcmVnMjAiLCJhIjoiY2tqaGsyNmVoNHp1cjJybGc2eWh3bjRrOSJ9.m_FtWyihSJ-QUYDzJwyZ6A"
              :map-options="{
                style: 'mapbox://styles/mapbox/streets-v10',
                center: [-5.573164,42.599031],
                zoom: 15
              }"
              :geolocate-control="{
                show: true,
                position: 'top-left'
              }"
              :scale-control="{
                show: true,
                position: 'top-left'
              }"
              :fullscreen-control="{
                show: true,
                position: 'top-left'
              }"
              :markers="[
                {
                  options: {
                    color: '#ff0000'
                  },
                  location: [-5.573164,42.599031],
                  popup: {
                    html: '<h3>IdeaLab</h3><a href=&quot;/application/sodaq-demo/device/sodaq-maker-2&quot;>path to idealab</a>'
                  }
                },
                {
                  location: [-5.573164,42.599031]
                }
              ]"
            ></Mapbox>
          </div>
          <v-btn 
              outlined
              depressed
              rounded
              v-bind:style="{color: colorTexto, fontSize: textSize + 'px', fontFamily: textFam}"
          >
          <div @click="ubicacion()" v-bind:style="{color: colorTexto, fontSize: textSize + 'px', fontFamily: textFam}">Recordar ubicacion</div>
        </v-btn>   
    </v-container>
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
import mapboxgl from 'mapbox-gl';
import Mapbox from '../components/MapBox'
export default {
  components: {
    Mapbox
  },
    data(){
        return{
          access_token: "sk.eyJ1IjoiYXBlcmVnMjAiLCJhIjoiY2tqaGs0ZXFmOTlpcjJ5bGJhYzk3d2pyaSJ9.qRsaUkXhCf94deljBI2MQQ",
          public_access_token: "pk.eyJ1IjoiYXBlcmVnMjAiLCJhIjoiY2tqaGsyNmVoNHp1cjJybGc2eWh3bjRrOSJ9.m_FtWyihSJ-QUYDzJwyZ6A",
          map:{},
          fondo1: "#64B5F6", 
          fondo2: "#90CAF9", 
          fondo3: "#BBDEFB",
          colorFondoTexto: "",
          tipoLetra: "",
          textSize: 20,
          textFam: "Helvetica",
          colorTexto: "white",
          fondoTexto: "transparent",
        }
    },

      /////////////////////
     //     MOUNTED     //
    /////////////////////
     mounted(){
      console.log("ESTOY ENN MOUNTED");
      console.log(this.$route.query.colorTexto);
      this.textSize = this.$route.query.textSize;
      this.textFam = this.$route.query.textFam;
      this.colorFondoTexto = this.$route.query.colorFondoTexto;
      this.createMap();
    },

      /////////////////////
     //      WATCH      //
    /////////////////////
    watch:{
      
      /****** CAMBIAR TIPOGRAFÍA ******/
      textFam:function() {
        if(this.textFam == "Por defecto"){
            this.textFam = "Helvetica";
        }
      },

      /****** CAMBIAR COLORES ******/
      colorFondoTexto:function() {
        switch(this.colorFondoTexto){
          case "Negro sobre Blanco":
            this.fondo1 = "#F5F5F5";
            this.fondo2 = "#FAFAFA"; 
            this.fondo3 = "#FFFFFF";
            this.colorTexto = "black";
            break;
          case "Blanco sobre Negro":
            this.fondo1 = "#212121"; 
            this.fondo2 = "#424242";
            this.fondo3 = "#616161";
            this.colorTexto = "white";
            break;
          case "Negro sobre Amarillo":
            this.fondo1 = "#FFEE58"; 
            this.fondo2 = "#FFF176";
            this.fondo3 = "#FFF9C4";
            this.colorTexto = "black";
            break;
          case "Amarillo sobre Negro":
            this.colorTexto = "yellow";
            this.fondo1 = "#212121"; 
            this.fondo2 = "#424242";
            this.fondo3 = "#616161";
            break; 
          case "Por defecto":
          default:
            this.fondoTexto = "transparent";
            this.colorTexto = "white";
            this.fondo1 = "#64B5F6"; 
            this.fondo2 = "#90CAF9";
            this.fondo3 = "#BBDEFB";
            break;
        }
      }
    },
    methods:{
      createMap(){
        mapboxgl.accessToken=this.public_access_token
        this.map= new mapboxgl.Map({
          container: 'map',   
          style: 'mapbox://styles/mapbox/streets-v11',
          zoom: 15.57,
          center: [-5.573164,42.599031]
        })
      },
  
      ubicacion(){
        this.map.addControl(new mapboxgl.GeolocateControl({
        positionOptions: {
            enableHighAccuracy: true
        },
        trackUserLocation: true
        }));
      }
    }
  }
</script>
<style>
#map {
  text-align: left;
  width: 100%;
  height: 100vh;
}
</style>
